.conteiner-svg {
    border: 2px solid $accent-color;
    border-right: none;
    width: 60px;
    height: 60px;
    position: relative;
}

.conteiner-svg::after {
  content: '';
  position: absolute;
  display: block;
  width: 2px;
  height: 10px;
  right: 0;
  top: -1px;
  background-color: $accent-color;
}

.conteiner-svg::before {
  content: '';
  position: absolute;
  display: block;
  width: 2px;
  height: 10px;
  right: 0;
  bottom: -1px;
  background-color: $accent-color;
}

.icon-svg {
  fill: $accent-color;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
}

